<template>
  <div class="model-list">
    <header class="header">
      <h1>Web Wide World</h1>
      <p>By Nobdy, For Everyone：平凡人共创普惠世界</p>
    </header>
<div class="baidu-search-box">
  <input v-model="modelUrl" type="text" class="baidu-input" placeholder="请填入场景 URL" @keydown.enter="goToViewer">
  <button class="baidu-btn" @click="goToViewer">
    Go
    <svg class="btn-icon" width="18" height="18" viewBox="0 0 20 20">
      <polyline points="7 5 13 10 7 15" fill="none" stroke="#fff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
    </svg>
  </button>
</div>
    <!-- <button class="pay-button" @click="goToPay">去支付</button> -->
    <div class="models-grid">
      <div 
      v-if="modelList.length > 0"
        v-for="model in modelList" 
        :key="model.id"
        class="model-card"
        @click="goToModel(model)"
      >
        <div class="model-preview">
          <div class="model-icon">🎯</div>
          <h3>{{ model.name }}</h3>
        </div>
        <div class="model-info">
          <!-- <p>{{ getModelDescription(model) }}</p> -->
          <button class="view-button">查看详情</button>
        </div>
      </div>
    </div>
    <!-- <SuccessDialog v-model:visible="show" message="支付成功" /> -->
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { ModelList } from '../configs'
// import SuccessDialog from '../components/successDialog.vue'
const router = useRouter()
const modelList = ref([])
const modelUrl = ref('')
const show = ref(false)
onMounted(async () => {
  console.log(12, useRoute().query);
  if (useRoute().query.isPay === 'true') {
    show.value = true
    setTimeout(() => {
      show.value = false
    }, 3000);
  }
  const requestFile = await fetch('https://mogs-1300354879.cos.ap-shanghai.myqcloud.com/mogs/timi-activity/gs-viewer/config/scene.json', {
      mode: 'cors',
      credentials: 'omit',
      cache: 'reload',
    });
    console.log(1111, requestFile);
        if (requestFile.status !== 200) {
      return new Error(`load  error ${requestFile.statusText},${requestFile.body}`);
    }
    const content = await requestFile.json();
    modelList.value = content.sceneConfig;
    console.log(`success file url content:`, content);
});


const getModelDisplayName = (model) => {
  return ModelList[model].name || model
}

const getModelDescription = (model) => {
  return ModelList[model].des || `${model} 3D模型`
}

const goToModel = (itemInfo) => {
  console.log('goToModel===', itemInfo);
  let url = `/viewer?modelUrl=${itemInfo.modelUrl}&interaction=true`;
  if (itemInfo.geometry && itemInfo.geometryPos) {
    url += `&geometry=${itemInfo.geometry}&geometryPos=${itemInfo.geometryPos}`;
  }
  if (itemInfo.modelPos) {
    url += `&modelPos=${itemInfo.modelPos}`;
  }
  if (itemInfo.name) {
    url += `&shareName=${itemInfo.name}`;
  }
  router.push(url)
}
const goToViewer = () => {
  console.log('goToViewer===');
  if (modelUrl.value) {
    return router.push(`/viewer?modelUrl=${modelUrl.value}`)
  }
}
const goToPay = () => {
  console.log('goToPay');
  // @ts-ignore
  window.wx.miniProgram.postMessage({ data: { action: 'pay', orderId: '12345' } });
  // @ts-ignore

  // wx.miniProgram.navigateBack();
  //  wx.miniProgram.navigateTo({url: '/pages/authorize/index', success: (res) => {
  //   console.log('navigateTo success', res);
  // }, fail: (res2) => {
  //   console.log('navigateTo fail', res2);
  // }})
 wx.miniProgram.redirectTo({url: '/pages/authorize/index'});
}
</script>

