import { EngineConfig } from '../../config/index.js';
import { SparkRenderController } from './spark-render-controller.js';
import { GS3dRenderController } from './gs-3d-render-controller.js';

export function getGaussianSplattingRender(sceneController) {
  const engine = localStorage.getItem('engine');
  console.log('get model controller ', engine);
  localStorage.removeItem('engine');
  switch (engine) {
    case EngineConfig.Spark:
      return new SparkRenderController(sceneController);
    case EngineConfig.GaussianSplats3D:
      return new GS3dRenderController(sceneController);
    default:
      return new GS3dRenderController(sceneController);
  }
}
