import * as THREE from 'three'; // 导入three.js核心库
import * as GaussianSplats3D from '@mkkellogg/gaussian-splats-3d';
import { StatsModule } from '../../compontent/stats.js';
import {
  getElementSize,
  getGSFileExt,
  getRotationMatrix,
  getWindowSize,
} from '../../../utils';

export default class SimpleViewer {
  constructor(container = null, scale = false) {
    this.container = container;
    this.scaleRenderer = scale;
    this.viewer = null;
    this.urls = [];
    this.stats = new StatsModule({
      containerId: 'js-simple-viewer',
      type: 0,
      style: { position: 'absolute', top: '0' },
    });
    // this.stats.show();
  }

  async init(url) {
    try {
      if (url === '' || !url) return;
      const configUrl = url;

      const config = await this.getSceneMataFile(configUrl);
      let rootElement = null;
      const renderer = null;

      // 如果指定了容器，添加到选项中
      if (this.container) {
        rootElement = document.querySelector(this.container);
        // renderer = this.initRenderer(rootElement);
        // renderer && rootElement.appendChild(renderer.domElement);
      }
      const viewerOptions = {
        rootElement,
        cameraUp: config?.cameraInfo?.lookUp || config?.lookUp,
        initialCameraPosition: config?.cameraInfo?.position || config?.position,
        initialCameraLookAt: config?.cameraInfo?.lookAt || config?.lookAt,
        sphericalHarmonicsDegree:
          config?.sphericalHarmonicsDegree >= 0
            ? config.sphericalHarmonicsDegree
            : 2,
        sharedMemoryForWorkers: false,
        renderer,
        showInfo: true,
        showLoadingUI: false,
        ...config?.gaussianSplats3DOption,
      };

      console.log('viewerOptions', viewerOptions);
      this.viewer = new GaussianSplats3D.Viewer(viewerOptions);

      // 判断 config.url 是不是数组
      if (config.url instanceof Array) {
        this.urls = config.url.map(item => ({
          url: item.path,
          show: false,
          position: item?.position,
          rotation: getRotationMatrix(item?.rotation),
          scale: item?.scale,
        }));
      } else {
        this.urls = [
          {
            url: config.url,
            show: false,
            position: config?.splashInfo?.position,
            rotation: getRotationMatrix(config?.splashInfo?.rotation),
            scale: config?.splashInfo?.scale,
          },
        ];
      }

      const start = new Date().getTime();
      await this.viewer.addSplatScene(this.urls[0].url, {
        format: getGSFileExt(this.urls[0].url),
        progressiveLoad: true,
        showLoadingUI: false,
        position: this.urls[0].position,
        rotation: this.urls[0].rotation,
        scale: this.urls[0].scale,
      });
      console.log('init time', new Date().getTime() - start);
      this.changeUrlState(this.urls[0].url, true);
      this.viewer.start();
      this.initThreeAxesHelper();
      this.update();
    } catch (error) {
      console.error('Failed to initialize GSViewer:', error);
    }
  }

  initThreeAxesHelper() {
    console.log('initThreeAxesHelper', this.viewer.threeScene);
    const axesHelper = new THREE.AxesHelper(50);
    this.viewer.threeScene.add(axesHelper);
  }

  initRenderer(container) {
    if (!this.scaleRenderer) return null;
    container.style.transform = 'scale(2)';
    container.style.transformOrigin = '0 0';
    const renderer = new THREE.WebGLRenderer({
      antialias: false,
      precision: 'highp',
    });
    // renderer.setPixelRatio(this.devicePixelRatio);
    renderer.autoClear = true;
    renderer.setClearColor(new THREE.Color(0x000000), 0.0);
    const size = container ? getElementSize(container) : getWindowSize();
    console.log('size', size);
    renderer.setSize(size.width / 2, size.height / 2);
    return renderer;
  }

  showInfoPanel(status) {
    this.viewer.showInfo = status;
    status ? this.viewer.infoPanel.show() : this.viewer.infoPanel.hide();
  }

  getUrls() {
    return this.urls;
  }

  update() {
    // console.log("update");
    requestAnimationFrame(this.update.bind(this));
    this.stats.stats.update();
  }

  async add(index) {
    const start = new Date().getTime();
    await this.viewer.addSplatScene(this.urls[index].url, {
      format: getGSFileExt(this.urls[index].url),
      progressiveLoad: true,
      showLoadingUI: false,
      position: this.urls[index].position,
      rotation: this.urls[index].rotation,
      scale: this.urls[index].scale,
    });
    console.log('add time', new Date().getTime() - start);
    console.log('add', this.viewer);
    this.changeUrlState(this.urls[index].url, true);
    return this.urls;
  }

  remove(index) {
    let splatIndex = -1;
    for (let i = 0; i <= index && i < this.urls.length; i++) {
      if (this.urls[i].show === true) {
        splatIndex += 1;
      }
    }
    this.viewer.removeSplatScene(splatIndex, false);
    this.changeUrlState(this.urls[index].url, false);
  }

  changeUrlState(path, show) {
    const urlItem = this.urls.find(item => item.url === path);
    urlItem.show = show;
  }

  // 添加重置相机方法
  resetCamera() {
    if (this.viewer && this.viewer.resetCamera) {
      this.viewer.resetCamera();
    }
  }

  // 添加销毁方法
  dispose() {
    if (this.viewer) {
      // this.viewer.dispose();
      this.viewer = null;
    }
  }

  async getSceneMataFile(url) {
    const requestFile = await fetch(url, {
      mode: 'cors',
      credentials: 'omit',
      cache: 'reload',
    });
    if (requestFile.status === 200) {
      const content = await requestFile.json();
      console.log('content', content);
      return content;
    }
  }
}
