<template>
  <div id="spark-viewer-container" ref="containerRef"></div>
</template>

<script setup>
import * as THREE from "three";
import { onMounted, onUnmounted, ref  } from 'vue'
import { useRoute } from 'vue-router';
import { SplatMesh, SparkControls } from "@sparkjsdev/spark";
import Stats from 'three/addons/libs/stats.module.js';
import { getSceneMataFile } from "../../infranstructure/utils/util";

const containerRef = ref(null);
let renderer = null;
let mesh = null;
let camera = null;
let controls = null;
let clock = null;
let stats = null;
const route = useRoute()
const modelUrl = ref(route.query.modelUrl);


// 窗口大小调整处理
function handleResize() {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  rendererInstance.setSize(window.innerWidth, window.innerHeight);
}
onMounted(async () => {
   if(!modelUrl.value) return;
  const metaFileConfig = await getSceneMataFile(decodeURIComponent(modelUrl.value));
  console.log("metaFileConfig", metaFileConfig);
  let url = metaFileConfig.url instanceof Array ? (metaFileConfig.url[0]?.path || metaFileConfig.url[0]) : metaFileConfig.url;

  const scene = new THREE.Scene();
  camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.1, 1000);
  camera.position.set(0, 0, 3);
  
  const rendererInstance = new THREE.WebGLRenderer();
  rendererInstance.setSize(window.innerWidth, window.innerHeight);
  containerRef.value.appendChild(rendererInstance.domElement);
  renderer = rendererInstance;

  // 创建时钟用于计算 deltaTime
  clock = new THREE.Clock();

  // 创建 FPS 监测器
  stats = new Stats();
  stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
  containerRef.value.appendChild(stats.dom);

  // 创建 SparkControls，确保 canvas 正确传递
  controls = new SparkControls({ canvas: rendererInstance.domElement });
  
  // 确保 canvas 可以接收焦点和事件
  const canvas = rendererInstance.domElement;
  canvas.tabIndex = 1; // 使 canvas 可以获得焦点
  canvas.style.outline = 'none'; // 移除焦点时的边框
  canvas.focus(); // 立即获得焦点

  const splat = new SplatMesh({ url });
  splat.quaternion.set(1, 0, 0, 0);
  splat.position.set(0, 0, 0);
  scene.add(splat);
  mesh = splat;
  window.addEventListener('resize', handleResize);

  // 点击 canvas 时确保获得焦点
  canvas.addEventListener('click', () => {
    canvas.focus();
  });

  rendererInstance.setAnimationLoop(function animate() {
    // 开始 FPS 监测
    stats.begin();
    
    const deltaTime = clock.getDelta();
    
    // 传递 deltaTime 给 SparkControls
    controls.update(camera);
    
    rendererInstance.render(scene, camera);
    
    // 结束 FPS 监测
    stats.end();
  });
});

onUnmounted(() => {
  if (renderer) {
    window.removeEventListener('resize', handleResize);
    renderer.dispose();
  }
  if (clock) {
    clock = null;
  }
  if (stats && stats.dom && stats.dom.parentNode) {
    stats.dom.parentNode.removeChild(stats.dom);
    stats = null;
  }
});
</script>



<style scoped>
#spark-viewer-container {
  width: 100vw;
  height: 100vh;
  background: #000;
}
</style>