import { SplatMesh, SparkRenderer, PackedSplats } from '@sparkjsdev/spark';
import GsRenderController from './gs-render-controller.js';
import { resizeEventListener } from '../../../../utils/window.js';

export class SparkRenderController extends GsRenderController {
  async render(config, container, wrapper) {
    console.log('init3DGS using spark render', config, container, wrapper);
    const splatConfigs = this.getConfig(config);
    for (const splatConfig of splatConfigs) {
      if (splatConfig.rendererScale !== 1) {
        this.setRenderScale(container, splatConfig.rendererScale, wrapper);
      }

      // resize
      resizeEventListener(
        this.camera,
        this.renderer,
        container,
        splatConfig.rendererScale,
      );
      this.loadMesh(splatConfig);
      // 创建SparkRenderer用于渲染环境贴图
      this.spark = new SparkRenderer({
        renderer: this.renderer,
        autoUpdate: false,
      });
      await this.loadSplatFile(splatConfig);
    }
  }

  async loadSplatFile(splatConfig) {
    console.log('loadSplatFile ', splatConfig);
    const packedSplats = new PackedSplats({ url: splatConfig.url });
    // 创建第一个背景
    const splatMesh = new SplatMesh({ packedSplats });
    splatMesh.rotateX(Math.PI);
    splatMesh.rotateY(Math.PI);
    if (splatConfig.position) {
      splatMesh.position.set(
        splatConfig.position[0],
        splatConfig.position[1],
        splatConfig.position[2],
      );
    }
    if (splatConfig.scale) {
      splatMesh.scale.set(
        splatConfig.scale[0],
        splatConfig.scale[1],
        splatConfig.scale[2],
      );
    }
    this.scene.add(splatMesh);
  }

  getUrls() {
    return this.splatConfigs;
  }

  animate() {
    this.spark?.update({ scene: this.scene });
  }
  onWindowResize() {
    super.onWindowResize();
    window.location.reload();
  }

  // 清理资源
  dispose() {}
}
