<template>
  <div class="model-detail">
    <header class="detail-header">
      <div class="detail-header-container">
        <button class="back-button" @click="goBack">← 返回列表</button>
        <h1>环境映射示例</h1>
      </div>
    </header>
    <div class="model-viewer" ref="sceneContainer">
      <div id="scene">
        <label
          ><input
            type="checkbox"
            @change="toggleMetal($event.target)"
            autocomplete="off"
          />
          塑料</label
        >
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import { useRouter } from "vue-router";
import * as THREE from "three";
import { SplatMesh, SparkRenderer, PackedSplats } from "@sparkjsdev/spark";
import { GLTFLoader } from "three/addons/loaders/GLTFLoader.js";
import {StatsModule} from "../infranstructure/compontent/stats.js";

const router = useRouter();
const sceneContainer = ref(null);

let scene, camera, renderer, spark, duck;
let background, background2;
let lastTime;
let renderedEnvMap = false;

const goBack = () => {
  router.push("/");
};

const toggleMetal = (el) => {
  const metal = el.checked;
  if (!duck) return;

  for (let obj of duck.children) {
    obj.material.metalness = metal ? 0.0 : 1.0;
    obj.material.roughness = metal ? 0.2 : 0.02;
  }
};

const initScene = async () => {
  // 创建场景
  scene = new THREE.Scene();
  camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000,
  );
  camera.position.y = 0.5;

  renderer = new THREE.WebGLRenderer();
  renderer.setClearColor(new THREE.Color(0x1b2037), 1.0);
  renderer.setSize(window.innerWidth, window.innerHeight);

  const sceneElement = document.getElementById("scene");
  sceneElement.appendChild(renderer.domElement);

  // 创建SparkRenderer用于渲染环境贴图
  spark = new SparkRenderer({ renderer, autoUpdate: false });

  // 加载高斯点云模型
  try {
    // 假设模型在public目录下
    const splatURL = "/models/area3.ksplat";
    const packedSplats = new PackedSplats({ url: splatURL });

    // 创建第一个背景
    background = new SplatMesh({ packedSplats });
    background.quaternion.set(1, 0, 0, 0);
    background.position.set(0.5, 0, -1);
    background.scale.setScalar(0.5);
    scene.add(background);

    // 创建第二个背景
    background2 = new SplatMesh({ packedSplats });
    background2.quaternion.set(1, 0, 0, 0);
    background2.rotation.y = Math.PI;
    background2.position.set(-0.5, 0, 0.0);
    background2.scale.setScalar(0.5);
    scene.add(background2);

    // 确保背景加载完成后再渲染环境贴图
    await background.initialized;
    await background2.initialized;

    // 加载橡皮鸭模型
    const gltfLoader = new GLTFLoader();
    const modelURL = "/models/rubberduck.glb";
    const gltf = await gltfLoader.loadAsync(modelURL);
    duck = gltf.scene;
    duck.position.set(0, 0.45, -0.4);
    scene.add(duck);
    const clock = new THREE.Clock();
    const state = new StatsModule({
      containerId: "app",
      type: 0,
      style: {
        position: "absolute",
        top: "0",
      },
    });
    state?.show();
    const gameLoop = ()=>{
      // console.log("game loop")
      renderer.render(scene, camera);
      spark.update({ scene });
      state?.stats.update();
      const deltaTime = clock.getDelta();
      if (duck) {
        duck.rotation.y += deltaTime / 2500;
      }
      // 确保只渲染一次环境贴图
      if (!renderedEnvMap && duck && background && background2) {
        renderedEnvMap = true;
        spark
            .renderEnvMap({
              scene,
              // 从鸭子位置渲染
              worldCenter: duck.position,
              // 隐藏鸭子以免遮挡环境
              hideObjects: [duck],
              // 非必需，防止"首次渲染"问题
              update: true,
            })
            .then((envMap) => {
              for (let obj of duck.children) {
                // 设置鸭子材质的反射属性
                obj.material.envMap = envMap;
                obj.material.original_color = obj.material.color.clone();
                obj.material.metalness = 1.0;
                obj.material.roughness = 0.02;
              }
            })
            .catch((err) => {
              console.error("渲染环境贴图失败:", err);
            });
      }
      requestAnimationFrame(gameLoop)
    }
    gameLoop()
    gameLoop()
  } catch (error) {
    console.error("加载模型失败:", error);
  }
};


const animate = (time) => {
  this.animationId = requestAnimationFrame(this.animate.bind(this));
  const deltaTime = time - (lastTime || time);
  lastTime = time;

  renderer.render(scene, camera);
  spark.update({ scene });

  if (duck) {
    duck.rotation.y += deltaTime / 2500;
  }

  // 确保只渲染一次环境贴图
  if (!renderedEnvMap && duck && background && background2) {
    renderedEnvMap = true;
    spark
      .renderEnvMap({
        scene,
        // 从鸭子位置渲染
        worldCenter: duck.position,
        // 隐藏鸭子以免遮挡环境
        hideObjects: [duck],
        // 非必需，防止"首次渲染"问题
        update: true,
      })
      .then((envMap) => {
        for (let obj of duck.children) {
          // 设置鸭子材质的反射属性
          obj.material.envMap = envMap;
          obj.material.original_color = obj.material.color.clone();
          obj.material.metalness = 1.0;
          obj.material.roughness = 0.02;
        }
      })
      .catch((err) => {
        console.error("渲染环境贴图失败:", err);
      });
  }
};

// 处理窗口大小变化
const handleResize = () => {
  if (camera && renderer) {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
  }
};

onMounted(async () => {
  await initScene();
  window.addEventListener("resize", handleResize);
});

onUnmounted(() => {
  // 清理资源
  window.removeEventListener("resize", handleResize);
  if (renderer) {
    renderer.setAnimationLoop(null);
    renderer.dispose();
  }
  if (scene) {
    scene.clear();
  }
});
</script>

<style scoped>
.model-detail {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
}

.detail-header {
  background: rgba(0, 0, 0, 0.8);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 10;
}

.detail-header-container {
  flex: 1;
  align-items: center;
  flex-direction: row;
  display: flex;
  gap: 1rem;
}

.back-button {
  padding: 0.5rem 1rem;
  background: rgba(108, 117, 125, 0.9);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background: rgba(90, 98, 104, 0.9);
}

.detail-header h1 {
  margin: 0;
  color: white;
  font-weight: 500;
  font-size: 1.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.model-viewer {
  flex: 1;
  position: relative;
  background: #000;
}

#scene {
  width: 100%;
  height: 100%;
  position: relative;
}

label {
  font-family: sans-serif;
  font-size: 20px;
  position: absolute;
  color: white;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  display: block;
  width: 100px;
  left: calc(50% - 50px);
  top: 10px;
  text-align: center;
  background-color: black;
  z-index: 100;
}
</style>
