import { createRouter, createWebHashHistory } from 'vue-router';
import ModelList from '../pages/ModelList.vue';
import ModelDetail from '../pages/ModelDetail.vue';
import ChunkedLoading from '../pages/ChunkedLoading.vue';
// import ScaleChunkedLoading from "../pages/ScaleChunkedLoading.vue";
import Metaverse from '../pages/Metaverse.vue';
import Spark from '../pages/Spark.vue';
// import Babylon from '../pages/test-gp/Babylon.vue';
import TestSpark from '../pages/test-gp/Spark.vue';
import Mkkellogg from '../pages/test-gp/Mkkellogg.vue';

const routes = [
  {
    path: '/',
    name: 'ModelList',
    component: ModelList,
    meta: { title: 'Web Wide World' }
  },
  {
    path: '/model',
    name: 'ModelDetail',
    component: ModelDetail,
    props: true,
  },
  {
    path: '/simple-viewer',
    name: 'ChunkedLoading',
    component: ChunkedLoading,
    props: true,
  },
  // {
  //   path: "/scale-simple-viewer",
  //   name: "ScaleChunkedLoading",
  //   component: ScaleChunkedLoading,
  //   props: true,
  // },
  {
    path: '/viewer',
    name: 'Viewer',
    component: Metaverse,
    props: true,
  },
  {
    path: '/spark',
    name: 'spark',
    component: Spark,
    props: true,
  },
  {
    path: '/metaverse-interaction',
    name: 'MetaverseInteraction',
    component: Metaverse,
    props: true,
  },
  {
    path: '/test-gp/babylon',
    name: 'TestGPBabylon',
    component: () => import('../pages/test-gp/Babylon.vue'),
    props: true,
  },
  {
    path: '/test-gp/spark',
    name: 'TestGPSpark',
    component: TestSpark,
    props: true,
  },
  {
    path: '/test-gp/mkkellogg',
    name: 'TestMkkellogg',
    component: Mkkellogg,
    props: true,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
export default router;
