import { SceneController } from './game/scene-controller.js';

class MetaViewer {
  constructor(configUrl, wrapper, container, ConfigOption) {
    console.log('场景初始化');
    this.scene = new SceneController(
      configUrl,
      wrapper,
      container,
      ConfigOption?.isFullscreen,
      ConfigOption?.closePanelCb,
    );
  }

  exitFullScreen() {
    this.scene.exitFullScreen();
  }

  async render() {
    await this.scene.render();
  }

  dispose() {
    this.scene.clear();
  }
}
export { MetaViewer };
