<template>
  <div id="mkkellogg-container" ref="containerRef">
    <div v-if="error" class="error-overlay">
      <div class="error-message">{{ error }}</div>
      <button @click="retry" class="retry-button">Retry</button>
    </div>
    <div class="controls-info">
      <div>Mouse: Drag to rotate, Scroll to zoom</div>
      <div>Touch: Drag to rotate, Pinch to zoom</div>
      <div>Keys: WASD to move</div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref } from 'vue'
import * as THREE from 'three'
import * as GaussianSplats3D from "@mkkellogg/gaussian-splats-3d";
import Stats from 'three/addons/libs/stats.module.js'
import { useRoute } from 'vue-router';
import { getSceneMataFile } from "../../infranstructure/utils/util";

const containerRef = ref(null)
const loading = ref(true)
const loadingProgress = ref(0)
const error = ref(null)
const route = useRoute()
const modelUrl = ref(route.query.modelUrl);

let viewerInstance = null
let stats = null
let animationId = null

// Custom load complete handler
function onLoadComplete() {
  loading.value = false
  console.log('Tokyo model loaded successfully!')
  
  // Start animation loop
  animate()
}

// Animation loop
function animate() {
  animationId = requestAnimationFrame(animate)
  
  if (stats) stats.begin()
  
  if (viewerInstance) {
    viewerInstance.update()
    viewerInstance.render()
  }
  
  if (stats) stats.end()
}

// Load model function
async function loadModel(modelUrl) {
  try {
    loading.value = true
    error.value = null
    loadingProgress.value = 0
    
    // Create viewer with configuration
    viewerInstance = new GaussianSplats3D.Viewer({
      'cameraUp': [0, -1, -0.6],
      'initialCameraPosition': [0, 10, 15],
      'initialCameraLookAt': [0, 0, 0],
      'ignoreDevicePixelRatio': 1,
      'dynamicScene': false,
      'webXRMode': GaussianSplats3D.WebXRMode.None,
      'renderMode': GaussianSplats3D.RenderMode.Always,
      'sceneRevealMode': GaussianSplats3D.SceneRevealMode.Instant,
      'antialiased': true,
      'focalAdjustment': 1.0,
      'logLevel': GaussianSplats3D.LogLevel.None,
      'sphericalHarmonicsOrder': 0,
      'enableOptionalEffects': true,
      'inMemoryCompressionLevel': 0,
      'freeIntermediateSplatData': false
    })
    
    // Initialize viewer
    await viewerInstance.init()
    
    // Add viewer to container
    // containerRef.value.appendChild(viewerInstance.getRenderDimensions().element)
    
    // Add stats
    stats = new Stats()
    stats.showPanel(0) // 0: fps, 1: ms, 2: mb
    window.document.body.appendChild(stats.dom)
    
    // Load the splat scene
    await viewerInstance.addSplatScene(modelUrl, {
      'progressiveLoad': true,
      'showLoadingUI': false,
      'position': [0, 0, 0],
      'rotation': [0, 0, 0, 1],
      'scale': [1, 1, 1],
      'splatAlphaRemovalThreshold': 1,
    })
    
    // Start the viewer
    viewerInstance.start()
    
    onLoadComplete()
    
  } catch (err) {
    console.error('Error loading model:', err)
    error.value = err.message || 'Failed to load Tokyo model'
    loading.value = false
  }
}

// Retry function
function retry() {
  if (viewerInstance) {
    viewerInstance.dispose()
    viewerInstance = null
  }
  if (stats && stats.dom && stats.dom.parentNode) {
    stats.dom.parentNode.removeChild(stats.dom)
    stats = null
  }
  loadModel()
}

// Handle window resize
function handleResize() {
  if (viewerInstance) {
    viewerInstance.setRenderDimensions(window.innerWidth, window.innerHeight)
  }
}

onMounted(async () => {
    if(!modelUrl.value) return;
  const metaFileConfig = await getSceneMataFile(decodeURIComponent(modelUrl.value));
  console.log("metaFileConfig", metaFileConfig);
  let url = metaFileConfig.url instanceof Array ? (metaFileConfig.url[0]?.path || metaFileConfig.url[0]) : metaFileConfig.url;
  // Add resize listener
  window.addEventListener('resize', handleResize)
  
  // Load the model
  await loadModel(url)
})

onUnmounted(() => {
  // Cancel animation frame
  if (animationId) {
    cancelAnimationFrame(animationId)
  }
  
  // Dispose viewer
  if (viewerInstance) {
    viewerInstance.dispose()
  }
  
  // Remove stats
  if (stats && stats.dom && stats.dom.parentNode) {
    stats.dom.parentNode.removeChild(stats.dom)
  }
  
  // Remove resize listener
  window.removeEventListener('resize', handleResize)
})
</script>

<style scoped>
#mkkellogg-container {
  width: 100vw;
  height: 100vh;
  background: #000;
  position: relative;
  overflow: hidden;
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid #333;
  border-top: 3px solid #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

.loading-text {
  color: white;
  font-size: 18px;
  margin-bottom: 10px;
  text-align: center;
}

.loading-progress {
  color: #ccc;
  font-size: 14px;
}

.error-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.error-message {
  color: #ff6b6b;
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;
  padding: 0 20px;
}

.retry-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.retry-button:hover {
  background: #0056b3;
}

.controls-info {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: white;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.7);
  padding: 10px;
  border-radius: 5px;
  z-index: 100;
}

.controls-info div {
  margin-bottom: 5px;
}

.controls-info div:last-child {
  margin-bottom: 0;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>