<template>
  <div class="model-detail">
    <header class="detail-header">
      <button class="back-button" @click="goBack">
        ← 返回列表
      </button>
      <h1>{{ getModelDisplayName(modelName || modelUrl) }}</h1>
      <div v-if="urlList.length">
        <button
            v-for="(item, index) in urlList"
            :key="index"
            class="model-item"
            :class="{ 'current': item.show }"
            @click="item.show ?  unloadModel(index):loadModel(index)">
          {{ index + 1  }}
        </button>
      </div>
    </header>

    <div class="model-viewer" ref="sceneContainer" id="metaverse">
      <div id="joystick-container">
        <div id="joystick"></div>
      </div>
      <div id="jump-button">跳跃</div>
      <div id="run-button">跑步</div>
      <canvas id="scene" ref="webgl"></canvas>
    </div>

  </div>
</template>

<script setup>
import {ref, onMounted, onUnmounted} from 'vue'
import {useRouter, useRoute} from 'vue-router'
import {ModelList} from '../configs'
import {MetaViewer} from "../infranstructure/viewers/gs-viewer";

const webgl = ref(null)

const router = useRouter()
const route = useRoute()
const sceneContainer = ref(null)
const modelName = ref(route.query.modelName)
const modelUrl = ref(route.query.modelUrl)
let viewer = null;
let modelController = null;

const urlList = ref([]);

const getModelDisplayName = (model) => {
  return ModelList[model]?.name || '模型'
}

const goBack = () => {
  router.push('/')
}

onMounted(async () => {
  let configUrl = '';
  if(modelUrl.value && modelUrl.value !== ''){
    configUrl = decodeURIComponent(modelUrl.value);
  }else if(modelName.value && modelName.value !== ''){
    configUrl = `/models/${modelName.value}/model.meta.json`;
  }
  viewer = new MetaViewer(configUrl, webgl.value);
  modelController = viewer.scene?.modelController;
  await viewer.render();
  urlList.value = modelController?.getUrls().map(item=> {return {url: item.url, show: item.show}});
  console.log(urlList.value);
})

onUnmounted(() => {
  viewer?.dispose()
})

const loadModel = async (index)=>{
  await modelController.add(index);
  urlList.value = modelController?.getUrls().map(item=> {return {url: item.url, show: item.show}});
}

const unloadModel = async (index)=>{
  await modelController.remove(index);
  urlList.value = modelController?.getUrls().map(item=> {return {url: item.url, show: item.show}});
}

</script>

<style scoped>
.model-detail {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #000;
}

.detail-header {
  background: rgba(0, 0, 0, 0.8);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 10;
}

.model-viewer {
  position: relative;
  z-index: 1;
  flex: 1;
  overflow: hidden;
}
.back-button {
  padding: 0.5rem 1rem;
  background: rgba(108, 117, 125, 0.9);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background: rgba(90, 98, 104, 0.9);
}
.detail-header h1 {
  margin: 0;
  color: white;
  font-weight: 500;
  font-size: 1.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}
.back-button:hover {
  background: rgba(90, 98, 104, 0.9);
}


#scene {
  width: 100%;
  height: 100%;
}


.model-item{
  margin-right: 0.2rem;
  font-size: 0.7rem;
  background: #000;
  color: white;
  border: 1px solid white;
}
.model-item.current {
  background: white;
  color: #000;
}
#joystick-container {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 120px;
  height: 120px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 60px;
  touch-action: none;
  display: none; /* 默认隐藏，在移动设备上显示 */
}

#joystick {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 30px;
  touch-action: none;
}

#jump-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
  font-size: 18px;
  touch-action: none;
  display: none; /* 默认隐藏，在移动设备上显示 */
}

#run-button {
  position: absolute;
  bottom: 20px;
  right: 120px;
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
  font-size: 18px;
  touch-action: none;
  display: none; /* 默认隐藏，在移动设备上显示 */
}
</style>
